@import './App.css';

.tier-page .content {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center content horizontally */
  justify-content: center; /* Center content vertically */
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

.service-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 8px;
  margin: 10px;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.icon {
  font-size: 3rem;
  color: #f55520;
  margin-bottom: 15px;
}

.cta-button {
  background-color: #f55520; /* Purple color with transparency */
  color: white;
  padding: 20px 40px;
  font-size: 20px;
  font-weight: bold;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  outline: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-decoration: none;
  display: inline-block;
  position: relative;
  overflow: hidden;
  z-index: 1;
  margin-top: 20px;
}




.cta-button::before {
  content: '';
  position: absolute;
  background-color: rgb(245,85,32);
  top: 0;
  left: 0;
  right: 100%;
  bottom: 0;
  z-index: -1;
  transition: right 0.3s ease;
}

.cta-button:hover, .cta-button:focus {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.cta-button:hover::before, .cta-button:focus::before {
  right: 0;
}

/* Flying star animation */
@keyframes flyStar {
  0% {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
  100% {
    opacity: 0;
    transform: translate(100px, -300px) scale(0);
  }
}

.cta-button:active .star {
  animation: flyStar 0.7s forwards ease-in;
}

.star {
  display: none; /* Hide the star by default */
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background-image: url('star.png');
  background-size: cover;
  transform: translate(-50%, -50%); /* Center the star */
}
