@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  font-family: 'Lato', sans-serif;
  margin: 0;
  padding: 0;
  background-image: url('image.png');
  background-repeat: repeat;
  color: white;
  min-height: 100vh; /* Ensure the minimum height is full viewport */
}

header , footer {
  background-color: rgba(255, 255, 255, 0.02); /* More transparent for glass effect */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Deeper shadow for depth */
  backdrop-filter: blur(20px); /* Increased blur for a more frosted look */
  -webkit-backdrop-filter: blur(20px); /* For Safari compatibility */
  color: white;
  padding: 20px 0;
  text-align: center;
}

header, footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; /* Allows items to wrap onto the next line */
  text-align: center;
}

.header-content {
  opacity: 1 !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

nav ul {
  list-style: none;
  padding: 0;
  display: flex;
}

nav ul li {
  margin: 0 10px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: #FFD600; /* Highlight color on hover */
}

.services {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 50px 5%; /* Adjusted padding for responsiveness */
  text-align: center;
}

.service {
  width: calc(33% - 20px); /* Adjust width for spacing */
  margin: 10px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.1); /* Light background for service cards */
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  transition: transform 0.3s ease;
}

.service:hover {
  transform: translateY(-5px); /* Slight raise effect on hover */
}

.service h2 {
  color: #FFD600; /* Title color */
}

.price {
  color: #4CAF50; /* Price color */
  font-weight: bold;
  font-size: 1.2em;
  margin-top: 10px; /* Space out the price from the description */
}

footer {
  background-color: rgba(255, 255, 255, 0.02); /* Match the header's transparency */
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.2); /* Shadow inside the footer */
  padding: 10px 0;
  position: absolute;
  position: sticky; /* changed from absolute to sticky */
  bottom: 0;
  width: 100%;
}

footer p {
  margin: 0; /* Removes default margin from p tag */
}

@media (max-width: 768px) {
  .header-content, .footer-content {
    flex-direction: column;
    align-items: center;
  }

  nav ul {
    flex-direction: column;
    align-items: center;
  }

  nav ul li {
    margin: 5px 0; /* Add vertical space between items */
  }
}

/* Media query for mobile devices */
@media (max-width: 480px) {
  .services {
    padding: 20px; /* Reduce padding on smaller screens */
  }

  .service {
    width: 100%; /* Make service cards full width */
    margin: 10px 0; /* Adjust margin for vertical layout */
  }
}

img {
  max-width: 100%;
  height: auto;
}

.header-content, .footer-content {
  display: flex;
  justify-content: space-around;
  width: 100%;
  flex-wrap: wrap;
}

/* Adjustments for smaller screens */
@media (max-width: 768px) {
  .header-content, .footer-content {
    flex-direction: column;
  }

  nav ul {
    padding: 0;
    margin-top: 20px;
  }

  nav ul li {
    margin: 0 5px;
  }
}

@media (max-width: 480px) {
  .header-content h1, .footer-content p {
    margin-bottom: 20px;
  }

  .services {
    padding: 20px;
  }

  .service {
    width: 100%;
  }
}

.services {
  display: flex;
  justify-content: center;
  padding: 20px;
  gap: 20px; /* Adds space between service cards */
}

.service {
  border: 1px solid #ddd; /* Light grey border */
  padding: 20px;
  border-radius: 8px; /* Rounded corners */
  text-align: center; /* Center the content */
  width: 300px; /* Fixed width for consistency */
}

.icon {
  font-size: 2rem; /* 2rem icon size for visibility */
  margin-bottom: 10px; /* Space below the icon */
}

.learn-more-btn {
  display: inline-block;
  background-color: #f55520; /* Primary blue */
  color: white; /* White text */
  padding: 10px 20px;
  border-radius: 5px; /* Rounded button edges */
  text-decoration: none; /* Removes underline from link */
  margin-top: 15px; /* Space above the button */
  transition: background-color 0.3s; /* Smooth background color transition on hover */
}

.learn-more-btn:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

html, body {
  height: 100%;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-content {
  flex: 1;
  /* Other styling here */
}

footer {
  flex-shrink: 0;
  /* Other styling here */
}



